*
    font-family: sans-serif

body
    padding: 0px
    padding-top: 60px
    margin: 0px

section
    position: relative
    width: 100%
    padding: 25px
    overflow: hidden
    box-sizing: border-box
    z-index: 1

.inverted
    color: white

.max-width
    width: 100%

.scale
    position: absolute
    left: 0px
    right: 0px
    top: 0px
    bottom: 0px
    z-index: -1
    object-fit: cover
    height: 100%
    width: 100%

.darken
    filter: brightness(0.15) opacity(0.6) drop-shadow(0px 0px #0F006C)

main
    max-width: 800px
    margin: 20px auto
    padding: 25px
    // background: #fff

    h1
        position: relative
        font-weight: 400

    p
        line-height: 1.6
        font-weight: 100
        text-align: justify

    .largebutton
        box-sizing: border-box
        display: block
        width: 100%
        padding: 25px
        text-align: center
        border-radius: 5px
        border: 1px solid #ddd
        background: #fff
        box-shadow: 0px 0px 5px #ddd
        transform: none
        transition: transform 0.2s
        text-decoration: none
        font-weight: 100
        font-size: 18px
        color: #333

        &:hover
            transform: scale(0.95, 1.05)

.topper
    position: relative
    width: 100%
    height: 260px
    overflow: hidden
    background: #0F006C
    z-index: -2

    img
        z-index: -1
        position: sticky
        top: 0px
        bottom: 0px
        left: 0px
        right: 0px
        object-fit: cover
        width: 100%
        height: calc(100vh - 100%)
        filter: brightness(50%) opacity(35%)
        // transform: perspective(5000px)

footer
    margin-top: 120px
    width: 100%
    height: 140px
    background: #f5f5f5
    color: #777
    display: flex
    align-items: center
    justify-content: center
    font-weight: 100
